.ImageSection {
    display: -webkit-inline-box;
    margin-top: 4rem;
    margin-bottom: 4rem;
    .img {
        width: 50vh;
        height: 100%;
        padding-right: 50px;
        img {
            width: 100%;
            object-fit: cover;
            height: 100%;
        }
    }

    .about-info {
        padding-right: 2rem;
        margin-left: 2rem;
        width: 100%;
        h4 {
            font-size: 2rem;
        }
        .about-text {
            width: 100%;
            padding: 1rem 0;
        }
        @media screen and (max-width: 1024px) {
            width: 60%;
        }
    }
    @media screen and (max-width: 1400px) {
        flex-direction: column;
        .about-info {
            margin-left: 0;
        }
    }
}

.about-details {
    display: flex;
    p {
        padding: 0.3rem 0;
    }
    .right-section {
        margin-left: 0.5rem;
    }
}

.btn {
    padding: 1rem 2rem;
    background-color: #037fff;
    outline: none;
    border: none;
    font-family: inherit;
    font-size: inherit;
    color: white;
    text-transform: uppercase;
    cursor: pointer;
    letter-spacing: 2px;
    margin-top: 1rem;
    position: relative;
    a{
        text-decoration: none;
        color: inherit;
    }
    &::after {
        position: absolute;
        content: "";
        width: 0;
        height: 0.3rem;
        left: 0;
        bottom: 0;
        background-color: #a4acc4;
        transition: all 0.1s ease-in-out;
    }
    &:hover::after {
        width: 100%;
    }
}

.skillsContainer {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 4rem;
    row-gap: 3rem;
    margin: 4rem 0;
    @media screen and (max-width: 1100px) {
        grid-template-columns: repeat(1, 1fr);
    }
}

.btn-port {
    padding: 0.6rem 2rem;
    background-color: #0381ff5b;
    border: none;
    outline: none;
    cursor: pointer;
    font-size: inherit;
    font-family: inherit;
    color: white;
    margin-bottom: 1rem;
    transition: all 0.3s ease-in-out;
    &:not(:last-child) {
        margin-right: 1rem;
    }
    &:hover {
        background-color: #037fff;
    }
    &:active {
        background-color: #037fff;
    }
    &:focus {
        background-color: #037fff;
    }
}

.buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 4rem 10rem;
    flex-wrap: wrap;

    @media screen and (max-width: 1270px) {
        margin: 2rem 5rem;
    }

    @media screen and (max-width: 1100px) {
        margin: 2rem 2rem;
    }
}

.root {
	width: 100%;
	margin: 70px 0px;
}

.typo{
	width: 100%;
}

.heading{
	display: flex;
	align-items: center;

	.icon_react{
		margin: 0 8px;
	}
}


.badges {
    display: flex;
    align-items: center;
    margin: 50px 0;

    a{
        margin: 10px;

        img{
            height: 130px;
            width: 130px;
        }
    }

    a:hover{
        background-color: #037fff;
    }

    .badge-Circle{
        padding: 2px 2px 0 2px;
        border-radius: 100%;
        img{
            border-radius: 100%;
        }
    }
}